<template>
    <div>
        <div class="header-box">
            <div class="logo-login-box">
                <div class="logo"  @click="gotoMainPage()">LEGO</div>

                <div class="user" v-if="loginStatus" @click="clickProfile() ">
                    <img class="user-img" v-if="user.userImg" :src="`http://localhost:${port}/images/${userAvatar}`" onerror="this.src=``" />

                    <!-- :src="`/images/${userAvatar}`" "http://localhost:8080/1613228452118.jpg"

                    :src="`${userAvatar}`"  
                    src="./1612982928920.jpg"   
                    :src="require(`${userAvatar}`)" !!!!!!!!!!!!!!!!!!!!!!!!!!!
                    "require(`@/assets/images/stooges/${item.img}.jpg`)
                    -->
                    <!-- 
                            ...!
                     -->
                    <a-avatar v-else class="primary-user-img" :size="78" icon="user"  />    
                    <a-drawer
                        :title="user.nickName"
                        placement="right"
                        :closable="false"
                        :visible="visible"
                        @close="onClose"
                        >
                        <p @click="clickMypage()">  </p>
                        <div class="tab-box my-page-box">
                            <p @click="gotoHomeTap(1)">home</p>
                            <p @click="gotoLikeTap(1)">QnA</p>
                        </div>  
                        <p @click="clickLikePage()"></p>
                        <div class="tab-box like-page-box">
                            <p @click="gotoHomeTap(2)">home</p>
                            <p @click="gotoLikeTap(2)">QnA</p>
                        </div>
                        <p @click="gotoUpdatePage()"> </p>
                        <p @click="logout()"></p>
                    </a-drawer>
                </div>

                <div class="login" v-else @click="gotoLogin()">LOGIN</div>
            </div>
            <div class="navBar">
                <!-- <div @click="gotoQNA1()" class="router"></div> -->

                <span class="router" @click="gotoMainPage()">Home</span>
                <span class="router" @click="gotoQNA()">QnA</span>

                <!-- <router-link :to= "{ name: 'BoardPage' , params: { boardNum : 2 } }" class="router"></router-link>
                <router-link :to= "{ name: 'BoardPage' , params: { boardNum : 3 } }" class="router"></router-link> -->
            </div>
            
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default{
    data() {
        return {
            port : 3000,
            userAvatar: this.$store.state.user.user.userImg,
            //userAvatar: this.user.userImg,
            visible: false,

            myVisible : 0,
            likeVisible : 0

        }
    },
    
    //  visible: false,   "./1612982928920.jpg"
    // userNickname: this.$store.state.user.user.nickName
    //C:\Vue_project\pet_board_2\server\public\images\1613283892391.jpg


    computed: {
        ...mapState('user', [
            'user',
            'loginStatus',
        ]),
         
    },
    async created() {
         console.log('==============  : created   ================');
         await this.$store.dispatch('post/getAskPostProcess');
     },
    updated() {
        console.log('================   : updated ================');
        console.log('user ?! =? ',this.user);
        console.log('user ?! =? ',this.loginStatus);
    },
   
    methods: {
        gotoMainPage() {
            this.$router.replace({
                name: 'MainPage',
            });
            console.log(" ");
            console.log("   ??", this.user);
            console.log("  true false",this.loginStatus);
        },
        // gotoQNA1(){
        //     console.log(" 1");
        //     this.$router.push({
        //         name: 'MainPage',
        //         params: {
        //             boardNum : 1
        //         }
        //     });
            
        // },
         gotoQNA() {
            this.$router.replace({
                name: 'BoardPage',
            });
        
        },
        // gotoQNA(){
        //     console.log(" 2");
        //     this.$router.push({
        //         name: 'BoardPage'
        //     });
            
        // },
        gotoLogin(){
            this.$router.push({
                path: '/login'
            })
        },
        clickProfile(){
            this.visible = true;
            
          
        },
        clickMypage(){
            if(this.myVisible === 0){
                //tab 
                document.querySelector('.my-page-box').style.display = "block";
                this.myVisible = 1;
            }else { // num 1 tab 
                document.querySelector('.my-page-box').style.display = "none";
                this.myVisible = 0;
            }
        },
        
        clickLikePage(){
            if(this.likeVisible === 0){
                //tab 
                document.querySelector('.like-page-box').style.display = "block";
                this.likeVisible = 1;
            }else { // num 1 tab 
                document.querySelector('.like-page-box').style.display = "none";
                this.likeVisible = 0;
            }

        },
        gotoHomeTap(num) { // Home    +  
            this.visible = false;
            this.$router.push({
                name:'TapHome',
                params: { roomNum : num }  // 1     2 
            })
        },
        gotoLikeTap(num){ // Ask    +  
            this.visible = false;
            this.$router.push({
                name:'TapAsk',
                params: { roomNum : num }  // 1    2 
            })
           
        },
        gotoUpdatePage(){ //   
            this.visible = false;
            this.$router.push({
                path: '/update-page',
            })
        },

        onClose() {
            this.visible = false;
        },

        logout() {  
            this.visible = false;
            console.log('  ');
            this.$store.dispatch('user/logoutProcess');  
            this.$router.push({path:'/',}); 
        }
    
       
    }
    
}
</script>

<style scoped>
    .header-box{
        margin-bottom : 50px;   
    }
    .logo-login-box{
        display: flex;
        width: 98%;

    }   
    .logo {
        font-size: 60px;
        font-weight : bold;
        color: pink;
        text-align: center;
        width: 90%;
        margin-left: 13%;
        text-shadow: 3px 1px rgb(255, 122, 122);
    }
    .login{
        width: 10%;
        padding: 10px;
        text-align: center;
        font-size: 30px;
        font-weight: bold;
        margin-top:20px;
        color: pink;
    }
    .login:hover {
        background-color: pink;
        color: white;
    }
    .user{
        width: 10%;
        margin: 10px;
        padding: 10px;
    }
    .user-img{
        width: 100px;
        height: 100px;
        border-radius: 50px; 
        object-fit: cover;
       
   
    }
    .primary-user-img{
        background-color: pink;
    }
    .navBar{
        margin-left:30px;
        margin-top: 20px;
        text-align: center;
        font-size: 20px;
        /* text-decoration:  */
    }
    .router{
        font-size: 20px;
        margin:10px;
        padding: 10px;
        /* font-weight: bold; */
        color:pink;
        text-decoration: none;
    }
    .router:hover{
        color: rgb(255, 122, 122);
      
    }


    .ddd{
        display: none;
    }

    .tab-box{
        display: none;
        box-sizing: border-box;
        margin : 10px 0px 10px 40px;
    }
    
</style>


