import Vue from 'vue'

import store from '../store/store'

import Router from 'vue-router'
import MainPage from '@/components/MainPage'
import LoginPage from '@/components/LoginPage'
import CreatePage from '@/components/CreatePage'
import BoardPage from '@/components/BoardPage'
import UpdatePage from '@/components/UpdatePage'
import MainDetailPage from '@/components/MainDetailPage'
import MyPage from '@/components/MyPage'
import RegisterPage from '@/components/RegisterPage'


import TapHome from '@/components/Tap/TapHome'
import TapAsk from '@/components/Tap/TapAsk'
import TapPreview from '@/components/Tap/TapPreview'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/register',
      name: 'RegisterPage',
      component : RegisterPage
    },
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage
    },
    {
      path: '/create',
      name: 'CreatePage',
      component: CreatePage,
      props: true
    },
    {
      // path: '/qna/:boardNum', qna  ..!
      path: '/qna',
      name: 'BoardPage',
      component: BoardPage
    },
    {
      path: '/update-page',
      name: 'UpdatePage',
      component: UpdatePage
    },
    {
      path: '/main-detail/:contentId/:roomNum',
      name: 'MainDetailPage',
      component: MainDetailPage,
      props: true
      // props true   props .  $route.params.contendId   
    },
    {
      path: '/my-page/:roomNum', // roomNum 1    2    
      name: 'MyPage',
      component: MyPage,
      props: true
    },
    {
      path: '/tap-home/:roomNum', // roomNum 1    2   
      name: 'TapHome',
      component: TapHome,
      props: true
    },
    {
      path: '/tap-ask/:roomNum', // roomNum 1    2   
      name: 'TapAsk',
      component: TapAsk,
      props: true
    },
    {
      path: '/tap-preview', 
      name: 'TapPreview',
      component: TapPreview,
      props: true
    },


  ]
})



