<template>
    <div class="change-container">
        <div class="profileBox" > 
            <div class="update-title">  </div> 
            <form action="" class="margin-20" method="post" enctype="multipart/form-data" >
                <div v-if="!previewImg">
                    <input type="file"  name="img" @change="onFileChange"  ref="profileImg" id="profileImg">
                    <a-button class="basicProfileBtn" @click="basicProfileChange">  </a-button>
                </div>
                <div v-else class="preview-img-box">
                    <img :src="previewImg" class="preview-img"/>
                    <div class="margin-20"> 
                        <a-button @click="removeImage" class="removeBtn"></a-button>
                        <a-button type="submit" @click="submitImage" class="submitBtn"></a-button>
                    </div>
                </div>
            </form>
        </div>

        <div class="nicknameBox " >
            <div class="update-title"> </div> 
            <a-input type="" class="changeInputSize" v-bind:value="userNickname" v-model="changeNickname"></a-input>
            <a-button v-if="checkOk"> !!</a-button>
            <a-button v-else-if="!checkOk"  @click="duplicateCheck()"></a-button>
            <div>
                <a-button class="margin-20" @click="nicknameUpdate()"> </a-button>
            </div>
            
        </div>
        <div class="pwBox">
            <div class="update-title"> </div>
            <div class="pwInputBox">
                <div class="width-300">
                    <a-input class="changeInputSize pwInput" type="password" placeholder=" " v-model="originalPw"></a-input>
                   <a-input class="changeInputSize pwInput" type="password" placeholder=" " v-model="newPw"></a-input>
                   <a-input class="changeInputSize pwInput" type="password" placeholder=" " v-model="reEnterPw"></a-input>
                   <a-button class="pwBtn" @click="updatePwBtn()"> </a-button>
                </div>
               
            </div>    
        </div>
    </div>

</template>



<script>

import { mapState } from 'vuex';

//https://okayoon.tistory.com/entry/vuejs-%EA%B8%B0%EC%B4%88-%EC%84%B8%EB%AF%B8%EB%82%98-%ED%9B%84%EA%B8%B0-3%ED%9A%8C%EC%B0%A8-%EC%BD%94%EB%94%A9%EC%95%A0%ED%94%8C


//https://www.mocky.io/v2/5cc8019d300000980a055e76
// function getBase64(img, callback) {
//   const reader = new FileReader();
//   reader.addEventListener('load', () => callback(reader.result));
//   reader.readAsDataURL(img);

//   reader.onerror = (error) =>{
//       console.log('base64 err~~~~~~~~~~~~~~~~~~~~~~~``');
//   }
// }

export default {
    data(){
        return {
            userId : this.$store.state.user.user.userId,
            previewImg: '',            //   
            profileImg: '',           // !! 
            userNickname: this.$store.state.user.user.nickName,
            changeNickname : this.$store.state.user.user.nickName, //  
            nicknameCheck : this.$store.state.update.nicknameCheck, // true or false //   true!! 
            checkOk : '', //     true

            password : this.$store.state.user.user.password,
            originalPw: '', // pw
            newPw: '',      // pw
            reEnterPw: '', //pw 


        }
    },
    computed : {
        ...mapState('user',[
            'user'
        ]),
    
    },
    watch : {
        changeNickname() {
            this.checkOk = false;
        }
    },
    methods: {
        onFileChange(e) {

             console.log('refs : ',this.$refs.profileImg);
            // this.profileImg = this.$refs.profileImg.files[0];
        

            console.log(' ',this.profileImg);
            
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage(files[0]); //  

            this.profileImg = this.$refs.profileImg.files[0];            
            console.log('',this.profileImg);
        },

        submitImage(){ //    

           
            const formData = new FormData();
            let userId = this.user.userId;
            formData.append('user_id',userId);
            formData.append('img', this.profileImg);
            


           
            this.$store.dispatch('user/profileUpdateProcess',{
                formData : formData,
            })
            this.$message.success('  .');
            this.$router.push({
                path: '/',
            });
            location.reload(); 

            // location.href = location.href;
            // history.go(0);  
            // axios.post('/api/userInfo/changeProfile', formData)
            // .then((res)=>{
            //  
            //     console.log(res);
            // })
            // .catch((err)=>{
            //    
            // });


           
            // this.$store.dispatch('user/profileChangeProcess',{  
            //     formData : options,
            //     config : config
            // })
            // console.log('    : ',this.profileImg);

           
            // formData.append('userImg',this.profileImg);



            //formData.append('profileImg',JSON.stringify( this.profileImg ));

            // console.log( '1 :',formData);

            //  for(let key of formData.entries()){en
            //     console.log('key => ',key);
            // }

          
            
            // axios.post('/single-file',
            //     formData, 
            //     {
            //         headers: {
            //             'Content-Type': 'multipart/form-data'
            //         }
            //     }).then(()=>{
            //         console.log('SUCCESS!!!!!!!!');
            //     }).catch(()=>{
            //         console.log('FAILUER!!!!!!!!!');

            //     });
        
        //   console.log(' => ',this.);


        },
        basicProfileChange(){ //      
            this.$store.dispatch('user/basicProfileProcess');

        },

        async duplicateCheck(){ //  

            if(!this.changeNickname){
                this.$message.info(' .');
            } else if(this.userNickname === this.changeNickname){
                this.$message.info(' .');
            } else {
                await this.$store.dispatch('update/nicknameCheckProcess',{
                    changeNickname : this.changeNickname
                });
        
                this.nicknameCheck = this.$store.state.update.nicknameCheck;
            
                if(this.nicknameCheck){ // this.nicknameCheck == true     ! 
                    this.$message.success('  !');
                    this.checkOk = true;
                }else{
                    this.$message.info('!!!!!!!!!!!!!!!!!!!!');
                    this.checkOk = false;
                }
            }  
        },

        async nicknameUpdate(){  // 

            if(this.checkOk){ //  !
                await this.$store.dispatch('update/updateNicknameProcess',{
                    userId : this.userId,
                    changeNickname : this.changeNickname
                });
                 this.$message.success('  !');
                 this.$router.push({
                     path: '/',
                 });
            } else { //   X
                this.$message.info('  ');
            }
           
        },


        updatePwBtn(){ //  

            //originalPw: '', // pw
            //newPw: '',      // pw
            //reEnterPw: '', //pw 
            if(this.originalPw === '' || this.newPw ==='' || this.reEnterPw ===''){
                 this.$message.info(' .');
            } else {
                if(this.password === this.originalPw){  //    
                    if(this.originalPw === this.newPw){
                        this.$message.info('    .');
                    } else if(this.newPw === this.reEnterPw) {
                        this.$store.dispatch('update/updatePwProcess',{
                            userId : this.userId,
                            newPassword : this.newPw
                        });
                        this.$message.success(' !');
                        this.$router.push({
                            path: '/',
                        });
                    } else {
                        this.$message.info('  .');
                    }
                } else {
                this.$message.info(' .');
                }
            }  
        },


        

        createImage(file) {
            var previewImg = new Image();
            var reader = new FileReader();
            var vm = this;

            reader.onload = (e) => {
                vm.previewImg = e.target.result;
            };
            reader.readAsDataURL(file);
        },

        removeImage: function (e) {
        this.previewImg = '';
        }






    }, 
}
</script>


<style scoped>
.margin-20{
    margin: 20px;
}
.avatar-uploader > .ant-upload {
  width: 128px;
  height: 128px;
}
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}
.change-container{
    display: flex;
    align-items: center;
    flex-direction: column ;
    text-align: center;
   
}
.preview-img-box{
    flex-direction: column;
}
.preview-img{
    width : 200px;
    height: 200px;
    border-radius: 100%;
    object-fit: cover;
     /*
        object-fit: contain;
        object-fit: cover;
        object-fit: none;
        object-fit: scale-down;    
    */
}
.uploader-box{
    margin : 50px;
}

.update-title{
    font-size:20px;
    font-weight: bold;
    margin: 10px;
    
}

.changeInputSize{
    width: 200px;
    margin:10px;
}
/* .basicProfileBtn {

}
.removeBtn { 
    /* width : 100px;
    height: 30px; */
    /* font-size: 20px; 
}

.submitBtn {

} */

.profileBox{   
    margin: 30px;
    width: 800px;
    height: 350px;
    border: 1px solid rgb(247, 216, 216);
    background:rgb(238, 227, 233);

}
.nicknameBox{
    margin: 30px;
    width: 800px;
    height: 200px;
    border: 1px solid rgb(247, 216, 216);
    background:rgb(238, 227, 233);
}
.pwBox{
    margin: 30px;
    width: 800px;
    height: 300px;
    border: 1px solid rgb(247, 216, 216);
    background:rgb(238, 227, 233);
}

.pwInputBox{
    display: flex;
    justify-content: center; 
    align-items: center;
    margin: 20px;
}


.width-300{
    width: 300px;
}

.pwBtn{
    margin: 10px;
}

    /* img{
        width: 30%;
        margin: auto;
        display: block;
        margin-bottom: 10px;
    } */
</style>


