<template>
    <div class="align-center">
      <div class="form-title">
        <h1 @click="duplicateCheckNickname()"></h1>
      </div>
      
      <!-- <div class="align-center"> -->
        
        <div  class="container">
          <a-form :form="form" @submit="handleSubmit" class="form-box" >
            <div class="input-box">
              <!--  -->
              
              <a-form-item >
                <span></span>
                  <a-input
                      v-decorator="[
                      'realname',
                      {
                        rules: [{ required: true, message: 'Please input your name!', }],
                      },
                      ]"
                  />
              </a-form-item>
              <!--  -->
              <a-form-item >
                <span></span>
                  <a-input class="input-nickname"  
                    :class="{ 'input-border-red' : !checkNickName } "
                    v-decorator="[
                      'nickname',
                      {
                        rules: [
                          { 
                            required: true, 
                            message: 'Please input your nickname!' 
                          },
                          {
                            validator:  checkNickName
                          },
                          ],
                      }
                    ]"
                    v-model="changeNick"
                    
                    type="name"
                  
                  />
                  <a-button v-if="checkNickName" class="ok-btn btn">OK</a-button>
                  <a-button v-else-if="!checkNickName" class="dupli-btn btn"  @click="duplicateCheckNickname()"></a-button> 
             </a-form-item>
           
              <!--   -->
              <a-form-item>
                <span></span>
                  <a-input class="input-id" :class="{ 'input-border-red' : !checkId } "
                      v-decorator="[
                      'id',
                      {
                        rules: [
                          { required: true, message: 'Please input your id!', whitespace: true }
                          
                        ],
                      },
                      ]"
                       v-model="changeId"
                  />
                  <a-button v-if="checkId" class="ok-btn btn">OK</a-button>
                  <a-button v-else-if="!checkId" class="dupli-btn btn" @click="duplicateCheckId()"></a-button>
              </a-form-item>

              <!--  -->
              <a-form-item has-feedback>
                <span></span>
                  <a-input
                      v-decorator="[
                        'password',
                        {
                          rules: [
                            {
                              required: true,
                              message: 'Please input your password!',
                            },
                            {
                              validator: validateToNextPassword,
                            },
                          ],
                        },
                      ]"
                      type="password"
                  />
              </a-form-item>

              <!--   -->
              <a-form-item has-feedback>
                <span> </span>
                  <a-input
                      v-decorator="[
                      'confirm',
                      {
                          rules: [
                            {
                              required: true,
                              message: 'Please confirm your password!',
                            },
                            {
                              validator: compareToFirstPassword,
                            },
                          ],
                      },
                      ]"
                      type="password"
                      @blur="handleConfirmBlur"
                  />
                </a-form-item>
              </div>

              <div class="btn-box">
                <a-form-item class="btn-box">
                  <a-button class="submit-btn"  type="primary" html-type="submit">
                    Register
                  </a-button>
                  <div class="btn-login" @click="goToLogin()">login</div>
                </a-form-item>
              </div>
          </a-form>
        </div>    
      </div>
    <!-- </div> -->
</template>

<script>

import { mapState } from 'vuex';

// const inputNickClass = document.querySelector('.input-nickname');
// const inputIdClass = document.querySelector('.input-id');



export default {
  
  data() {
  
    return {


      changeNick:'',  //   (v-model)
      changeId: '', //   (v-model)
      
      
  
      // newNickName : '',
      
      //    boolean..! (dom )
      checkNickName: '',
      checkId: '',

      // store     
      storeNicknameCheck: '',
      storeIdCheck: '',


      confirmDirty: false,
      // residences,
      // autoCompleteResult: [],
      // formItemLayout: {
      //   labelCol: {
      //     xs: { span: 24 },
      //     sm: { span: 8 },
      //   },
      //   wrapperCol: {
      //     xs: { span: 24 },
      //     sm: { span: 16 },
      //   },
      // },
      // tailFormItemLayout: {
      //   wrapperCol: {
      //     xs: {
      //       span: 24,
      //       offset: 0,
      //     },
      //     sm: {
      //       span: 16,
      //       offset: 8,
      //     },
      //   },
      // },
    }
  },

  watch : {
    
    changeNick() {
    
      this.checkNickName = false;
      console.log('   ~');
      // const inputNickClass = document.querySelector('.input-nickname');
      // inputNickClass.classList.add('input-border-red');
      // if(!this.checkNickName){
      //   inputNickClass.style.borderColor = red ;
      // }
      
    },
    changeId(){
      this.checkId = false;
      console.log('   ~');
      // const inputIdClass = document.querySelector('input-id');
  
    },
   
  },

  beforeCreate() {
    this.form = this.$form.createForm(this, { name: 'register' });
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      // validateFieldsAndScroll
      this.form.validateFields(async(err, values) => {
        if (!err) {
          if(!this.checkNickName){
            this.$message.info('   !');
            
          }else if(!this.checkId){
            this.$message.info('   !');
            
          }else {
            console.log('  ..!',values);
            await this.$store.dispatch('register/registerProcess',{
              name : values.realname,
              nickName : values.nickname,
              id : values.id,
              password : values.password, 
            });

            this.$message.success(' !!');

            this.$router.push({
              path: '/login',
            });

            // console.log(' => ',values.realname);
            // console.log(' => ',values.nickname);
            // console.log(' => ',values.id);
            // console.log(' => ', values.password);
            // console.log('       ..!   ..!!!');
            // console.log('        ~');

            // console.log('Received values of form: ', values);
          }
        }
      });
    },
    handleConfirmBlur(e) {
      const value = e.target.value;
      this.confirmDirty = this.confirmDirty || !!value;
    },
    handleCheckNick(){

    },
    compareToFirstPassword(rule, value, callback) {
      const form = this.form;
      if (value && value !== form.getFieldValue('password')) {
        
        callback('Two passwords that you enter is inconsistent!');
      } else {
        callback();
      }
    },
    validateToNextPassword(rule, value, callback) {
      
      const form = this.form;
      if (value && this.confirmDirty) {
        form.validateFields(['confirm'], { force: true });
      }
      callback();
    },
    

    async duplicateCheckNickname() {
     
      if(this.changeNick === ''){
        console.log(' !');
      } else { //   

        await this.$store.dispatch('update/nicknameCheckProcess',{
          changeNickname : this.changeNick
        });
        
        this.storeNicknameCheck = this.$store.state.update.nicknameCheck;

        if(this.storeNicknameCheck){ // this.storeNicknameCheck == true     ! 
          this.$message.success('  .');
          this.checkNickName = true;
        } else {
          this.$message.info('   .');
          this.checkNickName = false;
        }
      }
    },

    async duplicateCheckId(){

      if(this.changeId === ''){
        console.log(' ');
      } else { //   

        await this.$store.dispatch('update/idCheckProcess',{
          changeId: this.changeId
        });
        
        this.storeIdCheck = this.$store.state.update.idCheck;

        if(this.storeIdCheck){ // this.storeIdCheck == true     ! 
          this.$message.success(' !');
          this.checkId = true;
        } else {
          this.$message.info('   .');
          this.checkId = false;
        }
      }
    },

    goToLogin(){
      this.$router.push({
        path: '/login'
      })
    }
  
  },
};
</script>

<style scoped>

  .align-center{
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .form-title h1{
    text-align: center;
    color: rgb(250, 116, 138);
    margin-bottom: 20px;
  }
  .container{
    display: flex;
    justify-content: center; 
  }

  .form-box{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 30px;
    width: 600px;
    padding: 50px;
    border: 2px solid pink;
  }

  .input-box{
    width: 400px;
    /* display: flex;
    justify-content: center;
    flex-direction: column; */
    margin: 30px;
  }

  .btn-box{
    display: flex;
    justify-content: center;
    /* flex-direction: column; */
    /* flex-wrap: wrap; */  
  }

  .submit-btn{
    background-color:pink;
    border-color: pink;
    
  }
  .submit-btn:hover{
    background-color:white;
    /* border-color: white; */
    color: pink;
  }

  .btn-login{
    text-align: center;
    font-size: 17px;
  }
 

  .input-border-red{
    border-color: red;
  }
  
  .ok-btn,.dupli-btn{
    float: right;
  }  

  .ok-btn{
    color: white;
    background-color:rgb(250, 170, 184);
    border-color: white;
  }

  .dupli-btn:hover{
    color: palevioletred;
    background-color:white;
    border-color: palevioletred;
  }
</style>


