<template>
    <div>
                <!-- <div class="previewImg">image</div> -->
        <div class="previewContainer">
            <div v-for=" content of mainContentsPreview" v-bind:key="content.contentId">
                <div class="previewBox" @click="goToDetailPage(content.content_id , content.board_num)">
                    <img class="previewImg"  alt="" :src="`http://localhost:${port}/images/${content.preview_image}`">
                    <div class="previewText">
                        <div class="pre-title">{{content.title}}</div>
                        <!-- <div>{{content.context}}</div> -->
                        <div class="pre-nickname">{{content.nickname}}</div>
                        <div>
                            <span class="pre-view"><a-icon type="eye"   />{{ content.view_num }}</span>
                            <span class="pre-like"><a-icon type="heart" />{{ content.like_num }}</span>
                        </div>
                        <!-- <div ><a-icon type="heart" />{{content.like_num}}</div> -->
                        <div class="pre-date">{{$moment(content.create_at).format('YYYY-MM-DD a h:mm:ss')}}</div>
                        <!-- 
                            content.userId === user.userId -> 
                         -->
                    </div>
                </div>
            </div>
            <!-- <a-pagination v-model="current" :total="mainContentsPreview.length" show-less-items />    ...-->
        </div>
    </div>
</template>

<script>

import { mapState ,mapGetters} from 'vuex'

export default{
    data() {
        // const likeCheckList = likeList.filter(item => item.content_id === mainContentsPreview.content_id 
        // && item.board_num === 1 && item.user_id === user.user_id);
        return {   
            current: 1, 
            port : 3000,
            // likeCheckList: likeCheckList,
            //likeCheck : likeCheckList.like_check,
            likeCheck : 1,
        }
    },
    computed: {
        ...mapState('post',[
            // 'askContents',
            'mainContentsPreview',
        ]),
        ...mapState('user',[
            'user'
        ]),
        // ...mapState('like',[
        //     'likeList'
        // ]),
        ...mapGetters('post',[

        ])
    },
    
    mounted(){
        // ........ ...
        if(this.likeCheck === 1){ //   
            const likeClass = document.querySelector('.pre-like i');
            likeClass.classList.add('.like-icon');
        }
    },

    async created(){
        await this.$store.dispatch('post/getMainPostProcess');
    },

    methods: {
        
        goToDetailPage(content_id ,board_num){
            this.$store.dispatch('post/viewUpProcess',{ //  1 
                contentId : content_id,
                boardNum : board_num
            }); 
            this.$router.push({
                name: 'MainDetailPage',
                params: {
                    contentId : content_id,
                    roomNum : board_num
                }
            });
        },
        // heartToggle(){
        //     console.log('1. likeLis ..!',this.likeList);
        //     console.log('2. likeLis ..!',this.likeList.likeCheck);
        //     console.log('3. likeLis ..!',this.likeCheckList);
        //     console.log('4. likeLis ..!',likeCheckList);
        //     // likeCheck 1  ->  like_num  -1  likeChek 0 !!
        //     // likeCheck 0  -> like_num  +1  likeCheck 1 !! 
        //     // likeId , userId , likeCheck , contentId , boardNum   ..!!
            
        //     if(this.likeCheck === 1){ //   ->  ..!
            
        //         //(likeDownProcess)    
        //         //mainContentsPreview.like_num 1  / likeCheck 0  
        //         await this.$store.dispatch('like/likeDownProcess');
                


        //     }else{//  ->    ..!
        //         //(likeUpProcess)    mainContentsPreview.like_num 1  ..!
        //         await this.$store.dispatch('like/likeUpProcess');
        //     }

        // },
    }
}
</script>

<style scoped>
    .previewContainer{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        /* flex-direction: row; */
        margin: 10px;
        width: 100%;
        height: 100%; 
    }
    .previewBox{
        margin: 60px 10px;
        width: 400px;
        height: 400px;
    }
    .previewImg{
        width: 100%;
        height: 90%;
        border: 1px solid rgb(216, 213, 213);
        object-fit: cover;
    }
    .previewText{
        width: 100%;
        /* height: 30%;     */
        border: 1px solid rgb(216, 213, 213);
        border-top: none;
        text-align: center;
        font-size : 17px;
    }
    .previewText .pre-title{
        font-size: 20px;
        font-weight: bold;
        /* color: black; */
        overflow: hidden;  /*    */
        text-overflow: ellipsis; /* ...  */
        white-space: nowrap; /*   */
    }
    .pre-view{
        margin-right: 30px;
    }
    .pre-view i, .pre-like i{
        margin-right: 5px;
    }
    .like-icon{
    /* margin-right: 20px; */
    color:brown;
}
</style>


